@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    scroll-behavior: smooth;
}

:root {
    --color-primary: #2c3e50 !important;
    --color-secondary: #ffffff !important;
    --grey-color: #696969 !important;
    --light-blue: #f6f8f9 !important;
    --green-color: #00d563 !important;
}

body {
    overflow-x: hidden;
}

* {
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    font-family: 'Jost', sans-serif;
    letter-spacing: 1.5px;
}
.title {
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
}

.title h2 {
    color: var(--color-primary);
    font-weight: bold;
    letter-spacing: 2px;
    margin-bottom: 50px;
}

.title-inverted h2 {
    color: white !important;
    margin-bottom: 50px;
}

.title-bottom {
    background-color: var(--color-primary) !important;
    height: 5px;
    width: 200px;
    margin: auto;
    margin-bottom: 30px;
}

.title-bottom-inverted {
    background-color: var(--color-secondary) !important;
}

@media screen and (max-width: 500px) {
    .title h2 {
        font-size: 1.6rem;
    }
}
.my-card {
    max-width: 80%;
    margin: auto;
    margin-bottom: 30px;
    box-shadow: 0 0 10px 1px lightgrey;
}

.card-body {
    text-align: center !important;
    color: var(--color-primary) !important;
}

.card-body h5 {
    color: var(--color-primary) !important;
    font-weight: 600;
    width: 100%;
    margin: 0 auto;
    line-height: 1.3;
}

.card-body h6 {
    color: var(--color-primary) !important;
    font-weight: 400;
    margin-top: 10px;
}

.card-body a span {
    color: #ffffff;
}

.card-img-top {
    border-bottom: 5px solid var(--color-primary);
}

.card-body a {
    background-color: var(--green-color);
    font-weight: bold;
}

.card-body a.btn:hover {
    background-color: #ffffff;
    border: 1px solid;
    border-color: var(--green-color);
}

.card-body a.btn:focus {
    box-shadow: none !important;
}

.card-body a.btn:hover .btntext {
    color: var(--green-color);
}

.options {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 767px) {
    .my-card {
        max-width: 60%;
        margin: auto;
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 500px) {
    .card-body h5 {
        font-size: 18px;
    }
}

@media screen and (max-width: 400px) {
    .my-card {
        min-width: 90% !important;
    }
}
@media screen and (max-width: 683px) {
    #my-projects {
        scroll-margin-top: 95px;
    }
}

.my-projects {
    padding-top: 5vw;
    padding-bottom: 5vw;
}

.pagination {
    display: flex;
    justify-content: center;
    color: var(--color-primary);
}

.pagination * {
    color: var(--color-primary);
    border-color: var(--color-primary);
    outline: none;
}

.projects-bottom-svg {
    margin-bottom: -1px;
}

@media screen and (max-width: 500px) {
    .my-projects {
        padding-top: 10vw;
        padding-bottom: 10vw;
    }
}
.main i {
    font-size: 35px !important;
}

div p {
    font-size: 20px;
    margin-top: 5px;
    text-decoration: none !important;
}

.main {
    margin-top: 30px;
}

@media screen and (max-width: 767px) {
    .main {
        display: flex;
        justify-content: center;
    }
    .main i {
        margin-right: 10px;
        font-size: 25px !important;
    }
    .main p {
        margin-top: 0px;
        line-height: 25px;
    }
}
.contact-me {
    color: var(--grey-color);
    text-align: center;
    background-color: var(--light-blue);
    padding-top: 5vw;
}

.footer {
    padding-bottom: 15px;
    padding-top: 50px;
}

@media screen and (max-width: 500px) {
    .contact-me {
        padding-top: 10vw;
    }
}
.work-tab {
    border: 5px solid;
    border-color: var(--color-secondary);
    width: 60%;
    margin: auto;
    margin-bottom: 20px;
    background-color: var(--color-secondary);
    color: var(--color-primary);
    font-weight: bold;
    border-radius: 20px;
    min-height: 350px;
    box-shadow: 5px 5px 10px 1px lightgrey;
}

.circle {
    display: none;
}

.resp-list {
    font-size: 20px;
    font-weight: 400;
    color: var(--grey-color);
    margin-top: 20px;
}

.work-tab h4,
h5 {
    text-align: center;
    color: var(--color-primary);
}

.work-tab h4 {
    font-weight: 600;
    margin-top: 5px;
}

.work-tab h5 {
    font-weight: 500;
    font-size: 22px;
    color: var(--grey-color);
    margin-top: 10px;
}

.year,
.skills {
    display: none;
}

@media screen and (max-width: 991px) {
    .work-tab {
        width: 80%;
        min-height: 0;
        color: var(--color-secondary);
        border: none;
        border-left: 2px solid var(--grey-color);
        border-radius: 0px;
        margin: 0 auto;
        padding-bottom: 30px;
        margin-bottom: 20px;
        background-color: var(--light-blue);
        box-shadow: none;
    }
    .skills {
        display: block;
    }
    .year {
        position: relative;
        display: block;
        width: 80%;
        color: var(--grey-color);
        font-weight: 600;
        margin: 0 auto;
        margin-top: 40px;
    }
    .year-data {
        position: absolute;
        margin-top: -25px;
        margin-left: -15px;
    }
    .circle {
        display: flex;
        width: 20px;
        height: 20px;
        border-radius: 15px;
        position: absolute;
        margin-left: -11px;
        margin-top: 15px;
        border: 2px solid var(--color-primary);
        background-color: var(--light-blue);
    }
    .work-tab h4,
    h5 {
        margin-left: 30px;
    }
    .resp-list {
        display: none;
    }
}

@media screen and (max-width: 500px) {
    .work-tab h4 {
        font-size: 20px;
        max-width: 90%;
        margin: 0 auto;
        margin-top: 5px;
    }
    .work-tab h5 {
        font-size: 18px;
        margin-top: 10px;
    }
    .work-tab {
        width: 90%;
        min-height: 0;
    }
    .year {
        width: 90%;
    }
    ul {
        font-size: 16px;
    }
}
    .experience {
        background-color: var(--light-blue);
        padding-top: 5vw;
        padding-bottom: 5vw;
    }
    
    .experience .work-row {
        display: none;
    }
    
    .we-svg-top,
    .we-svg-bottom {
        margin-top: -1px;
    }
    
    .sr-only {
        background-color: var(--color-primary);
    }
    
    .we-filler {
        display: none;
    }
    /* .carousel-control-next span {
        background-color: var(--color-primary) !important;
    } */
    
    .carousel-control-next-icon {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%2c3e50' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e") !important;
    }
    
    .carousel-control-prev-icon {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%2c3e50' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e") !important;
    }
    
    @media screen and (max-width: 683px) {
        .experience {
            scroll-margin-top: 95px;
        }
    }
    
    @media screen and (max-width: 991px) {
        .experience .work-row {
            display: block;
            margin-top: 50px;
        }
        #carouselExampleIndicators {
            display: none;
        }
    }
    
    @media screen and (max-width: 500px) {
        .experience {
            scroll-margin-top: 55px;
            padding-top: 10vw;
            padding-bottom: 10vw;
        }
        .experience h2 {
            font-size: 1.6rem;
        }
        .we-filler {
            display: block;
            background-color: var(--color-primary);
            height: 15px;
        }
    }
.profile {
    align-items: center;
    padding-top: 10vw;
    padding-bottom: 10vw;
    margin-top: 0px;
    background-color: var(--color-secondary);
}

.profile p,
h3 {
    margin: auto;
    text-align: center;
    margin-bottom: 15px;
}

.profile p {
    color: var(--color-primary);
}

.profile h3 {
    width: 60%;
    color: var(--grey-color);
}

.profile h3 {
    width: 75%;
    margin-top: 30px;
}

.profile p {
    font-weight: bold;
    font-size: 60px;
}

.profile h3 {
    font-weight: 300;
    font-size: 30px;
}

.profile-btntext {
    color: var(--color-secondary);
}

.profile-btn {
    background-color: var(--color-primary);
    padding: 15px;
    display: block;
    margin: auto;
    width: 200px;
    margin-top: 30px;
    border-radius: 5px;
    color: var(--color-secondary);
}

.profile-btn:hover {
    text-decoration: none;
    background-color: #ffffff;
    border: 1px solid;
    border-color: var(--color-primary);
    color: var(--color-primary);
}

.svg {
    margin-top: -1px;
}

@media screen and (max-width: 700px) {
    .profile h3 {
        width: 90%;
    }
    .profile p {
        font-size: 45px;
    }
}

@media screen and (max-width: 683px) {
    #my-projects {
        scroll-margin-top: 95px;
    }
}

@media screen and (max-width: 500px) {
    #my-projects {
        scroll-margin-top: 55px;
    }
    .profile p {
        font-size: 40px;
    }
    .profile h3 {
        font-size: 22px;
    }
    .profile {
        padding-top: 20vw;
        padding-bottom: 20vw;
    }
    .profile-btn {
        padding: 10px;
        width: 180px;
    }
    .profile h3 {
        width: 90%;
    }
}
#myBtn {
    position: fixed;
    bottom: 50px;
    right: 5px;
    z-index: 99999;
    outline: none;
    background-color: var(--color-secondary);
    color: var(--color-primary);
    cursor: pointer;
    height: 50px;
    width: 50px;
    justify-content: space-around;
    align-items: center;
    border-radius: 50%;
    display: flex;
    border: 1px solid;
    border-color: var(--color-primary);
}

.fa {
    font-size: 25px !important;
}

#myBtn:active {
    background-color: var(--color-primary);
    color: var(--color-secondary);
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}

.progress-container {
    background-color: var(--color-secondary);
    height: 10px;
    width: 100%;
}

.progressBar {
    background-color: var(--color-primary);
    height: 10px;
    width: 0%;
}
